.right-container {
  background-color: #aabbcc; }

.right-reflex-container {
  height: 100%;
  background-color: #aabbcc; }

/* Basic re-flex panel/splitter styling */
.reflex-element {
  overflow: hidden !important; }

.handle {
  background: #eae9e9;
  padding: 8px 0 0 8px;
  height: 28px; }

.reflex-splitter {
  -webkit-transition: background-color 0.5s ease !important;
  transition: background-color 0.5s ease !important;
  background-color: #535353 !important; }

.reflex-splitter:hover {
  background-color: #919191 !important; }

.reflex-layout.vertical > .reflex-splitter {
  border-right: 0px solid #c6c6c6 !important;
  border-left: 0px solid #c6c6c6 !important;
  width: 12px !important; }

.reflex-layout.horizontal > .reflex-splitter {
  border-top: 0px solid #c6c6c6 !important;
  border-bottom: 0px solid #c6c6c6 !important;
  height: 12px !important; }

.reflex-splitter {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center; }

