  padding-left: 4px;
  background-color: #535353;
  color: #ADADAD;
  margin-top: 2px;
  width: 100%; }

.filter-bar {
  height: 20px;
  padding-left: 6px;
  padding-right: 6px; }

.asset-node:hover {
  cursor: -webkit-grab;
  cursor: grab; }

.btn-container {
  display: table;
  margin-left: auto;
  margin-right: 0;
  width: auto; }

.btn-asset-action {
  display: table-cell;
  margin-left: 2px;
  margin-right: 2px;
  padding: 2px; }

