.alpha-warning-modal-button {
  font-family: Nunito; }

#alpha-modal {
  width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -250px;
  margin-left: -250px; }

.modal-backdrop {
  background-color: #000; }

.modal-content {
  background-color: #464646;
  color: #FFFFFF; }

.modal-footer {
  border-top: 1px solid #535353; }

.modal-header {
  border-bottom: 1px solid #535353; }

.alpha-warning-header .close {
  display: none; }

